import styled from 'styled-components';

import { BREAKPOINT_LG,MAIN_HEADER_COLOR,BUTTON_RADIUS,HEADER_COLOR,BORDER_COLOR,DARK_BORDER_COLOR,FONT_FAMILY_HEADER,ICON_COLOR,LIGHT_BACKGROUND,BODY_BACKGROUND,FADE_FONT_COLOR,HEADER_BACKGROUND,MAX_WIDTH,FONT_FAMILY,SECONDARY_FONT_COLOR,FONT_COLOR, BREAKPOINT_MD, BREAKPOINT_SM} from '../types';
const PageWrapper = styled.div`
         background:#fff;
         .page-body{
         	    padding: 0px 130px;
         	     @media screen and (max-width: ${BREAKPOINT_LG}) {padding: 0px 0px;}
         	h4 {
			  font-size: 21px;
			  font-weight: 400;
			  font-family:${FONT_FAMILY_HEADER};
			  margin-bottom: 0px;
			  color: ${HEADER_COLOR};
			  margin-top: 15px;
			  margin-bottom: 15px;
			  &:before {
			    display: block;
			    height: 6rem;
			    margin-top: -6rem;
			    visibility: hidden;
			    content: "";
			  }
			}
			h3 {
			    font-size: 24px;
			    color:${HEADER_COLOR};
			    font-family:${FONT_FAMILY_HEADER};
			    margin-bottom: 0px;
			    margin-top: 25px;
			    margin-bottom: -10px;
				font-weight:500;
			    &:before {
			      display: block;
			      height: 6rem;
			      margin-top: -6rem;
			      visibility: hidden;
			      content: "";
			    }
			  }
			div {
			  font-size: 15px;
			  margin-top: 15px;margin-bottom: 40px;
			  color: ${ICON_COLOR};
			}
			pre {
			    white-space: pre-line;
			    margin: 0px;
			}
			max-width: ${MAX_WIDTH};
		    margin: 0px auto;
		    margin-top: 0;
		    padding-bottom: 50px;
		    @media screen and (max-width: ${BREAKPOINT_MD}) {
				div {
			      font-size: 14px;
			    }
			    h3 {
			      font-size: 18px;
			    }
		    }
		    .page-body-in{
		    	@media screen and (max-width: ${BREAKPOINT_LG}) {
					margin-top: 0px!important;
		    	}
		    	code.hljs {
				    padding: 1em;
				}
				code.display {
				    border-top-left-radius: 0px;
				    border-top-right-radius: 0px;
				}
				code.side {
				      color: #c7254e;
    background: #f9f2f4;
    padding: 1px 5px!important;
    font-size: 13px;
    display: -webkit-inline-box!important;
    display: -webkit-inline-flex!important;
    display: -ms-inline-flexbox!important;
    display: inline-flex!important;
    border-radius: ${BUTTON_RADIUS};
				 
				}
				.tab {
					  padding-left: 15px;
					}

					.tab-2 {
					  padding-left: 30px;
					}
				span.hljs-tag {
				    margin-bottom: 2px;
				    display: inline-block;
				}
		    }

         }
         .page-head {
			  p {
			    font-size: 16px;
			    padding: 0px 40px;
			    color:rgba(255,255,255,0.6);
			    margin-top: 0px;
			    font-weight:400;
			  }
			   background-color: ${HEADER_BACKGROUND};
			 
			  h1 {
			    font-size: 36px;
			    font-weight:700;
			    font-family:${FONT_FAMILY_HEADER};
			    color:  ${MAIN_HEADER_COLOR};
			    margin-bottom: 10px;
			    margin-top: 0;
			    text-align: left;
			    padding: 0px 40px;
			    line-height: 60px;

			  }
			  .page-head-in {
				  max-width: ${MAX_WIDTH};
				  margin: 0 auto;
				}
				.page-head-bg{
					 background-color:${HEADER_BACKGROUND};
   		  background-size:100%; 
			  padding:40px 150px;
			  border-bottom:1px solid ${BORDER_COLOR};
					padding-top:110px;
			  @media screen and (max-width: ${BREAKPOINT_LG}) {
					background-size: 200%;
					padding:50px 0px;
					padding-top:130px;
					h1, p {
				      padding: 0px;
				    }
				    .page-head-in {
					   padding: 0px 20px;
					}
					p{font-size: 16px;

					}
					h1 {
					    font-size: 32px;
					  }
				}
				@media screen and (max-width: ${BREAKPOINT_MD}) {
					background-size: 300%;
					h1 {
				      font-size: 24px;
				      line-height: 1.1;
				    }
				    p {
				      font-size: 16px;
				    }
				}
				@media screen and (max-width: ${BREAKPOINT_SM}) {
					background-size: 400%;
					
				    p {
				      font-size: 15px;
				    }
				}
				}
				
		}  
         @media screen and (max-width: ${BREAKPOINT_LG}) {
         	.page-cont-in {
				padding: 0px;
			}
         } 
        `;
export default PageWrapper;